body {
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: rgb(188, 188, 188);
}

textarea::-webkit-scrollbar {
  width: 5px;
  background-color: #eae5e5;
}

textarea::-webkit-scrollbar-thumb {
  background: rgb(137, 137, 137);
  border-radius: 50px;
  /* border-radius: 10px; */
}

#btn1 {
  position: absolute;
  bottom: 22vh;
  border: none;
  font-size: 1.3vw;
  border-radius: 10px;
  margin-left: 48vw;
  padding: 10px 25px 10px 25px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

:hover.btn {
  background-color: rgb(230, 230, 230);
}

textarea {
  border: none;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 20px 25px -5px, rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
  appearance: none;
  outline: none;
  background: none;
  list-style: none;
  background-color: rgb(232, 232, 232);
  text-align: center;
  margin: 0vw 3vw 0vw 9vw;
  position: absolute;
  top: 25vh;
  width: 80vw;

  padding: 15px;
  font-size: 1.2vw;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  height: 40vh;
  border-radius: 10px;
}

ul {
  padding-left: 35vw;
  list-style: none;
}

.navbar {
  justify-content: center;
  position: absolute;
  align-items: center;
  width: 100vw;
  height: 16vh;
  background-color: rgb(0, 0, 0);
}

li {
  color: white;
  background-color: rgb(0, 0, 0);
  border-radius: 8px;
  text-align: center;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 1.2vw;
  width: 4vw;
  margin-top: 4vh;
  padding: 0.5vw;
  /* padding: 0.3vw 0.3vw 0.3vw 0.3vw; */
  margin-left: 30px;
  float: left;
}

:hover.li {
  animation: li 0.4s linear;
  background-color: rgb(255, 255, 255);
  color: black;



}

@keyframes li {
  0% {
    background-color: rgb(0, 0, 0);
    color: rgb(0, 0, 0);
    /* color: rgb(255, 255, 255); */

  }

  100% {
    background-color: rgb(255, 255, 255);
    color: black;
    /* color: black; */
  }

}

#edit {
  position: absolute;
  bottom: 14vh;
  border: none;
  box-shadow: rgba(17, 17, 26, 0.1) 0px 4px 16px, rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 56px;
  font-size: 1.2vw;
  border-radius: 10px;
  margin-left: 46vw;
  background: rgb(243, 243, 243);
  padding: 9px 23px 10px 25px;
  outline: none;
  scroll-behavior: smooth;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}


select#edit {
  border: none;
}

:hover#edit {
  background-color: rgb(230, 230, 230);
}

.inp {
  height: 2rem;
  border: none;
  outline: none;
  border-radius: 10px;
  font-size: 1.2vw;
  text-align: center;
  width: 170px;
  scale: 0;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

}

#replaceinp {
  position: absolute;
  bottom: 100px;
  left: 49.4vw;
}

#replaceinp2 {
  position: absolute;
  bottom: 100px;
  left: 62vw;
}

@media (min-width:0px) and (max-width:500px) {

  li {
    font-size: 4vw;
    /* margin-right: 50px; */
    width: 12vw;
  }

  :hover.li {
    padding-right: 10px;
  }

  textarea {
    width: 70vw;
    font-size: 3.4vw;
  }

  #btn1 {
    font-size: 3.7vw;
    margin-left: 35vw;
    cursor: pointer;
    bottom: 20vh;
  }

  #edit {
    font-size: 3.7vw;
    margin-left: 28vw;
    bottom: 12vh;
  }

  ul {
    padding-left: 0vw;
  }

  .inp {
    font-size: 3.7vw;
  }

  #replaceinp {
    bottom: 70px;
    left: 32vw;

  }

  #replaceinp2 {
    bottom: 27px;
    left: 32vw;

  }

}